<template>
  <div class="create">
    <form @submit.prevent="createProduct" method="post">

      <p>
        <label for="name">Name: </label><br>
          <input v-model="product.name" type="text" name="name" id="name" required="required">
      </p>

      <p>
        <label for="password">Password: </label><br>
        <input v-model="product.password" type="password" name="password" id="password" required="required">
      </p>

      <p>
        <label for="quantity">Quantity: </label><br>
          <input v-model="product.quantity" type="number" name="quantity" min="0" id="quantity" required="required"
                 oninput="validity.valid||(value='');">
      </p>

      <p>
        <label for="price">Price: </label><br>
          <input v-model="product.price" type="number" name="price" min="0" id="price" required="required"
                 oninput="validity.valid||(value='');">
      </p>

      <p>
        <label for="role">Role: </label><br>
        <input v-model="product.role" type="text" name="role" id="role" required="required">
      </p>

      <p>
        <input type="submit" value="Create">
      </p>

    </form>
  </div>
</template>

<script>
const {postRequest} = require("@/restApi/ApiCaller");
export default {
  name: "Get",
  data: () => ({
    product: {
      name: '',
      password: '',
      quantity: '',
      price: '',
      role: '',
    }
  }),
  methods: {
    createProduct() {
      postRequest("api/user/create", this.product);
    }
  }
}
</script>